.form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  align-items: center;
}
.formLabel {
  display: flex;
  flex-direction: column;
  gap: 5px;
  font-size: 18px;
  font-weight: 700;
  font-size: 1.5em;
  text-decoration: none;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
  font-family: Iceland;
  color: #228dff;
}

.formInput {
  width: 250px;
  height: 20px;
  border-radius: 4px;
  border: none;
  font-size: 18px;
  font-weight: 600;
  color: rgb(114, 114, 114);
  padding: 5px 10px;
  outline: transparent;
}

.logInBtn {
  border-radius: 4px;
  padding: 10px 30px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  text-align: center;
  font-size: 22px;
  text-decoration: none;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
  font-family: Iceland;
  color: #228dff;
}

.logInBtn:hover,
.logInBtn:focus {
  background-color: #228dff;
  color: #fff;
  transform: scale(1.1);
  transition: all 250ms;
}
